.form-container-product{
    border: 0.2px solid rgb(201, 201, 201);
    padding: 2em;
    border-radius: 2em;
    margin-bottom: 2em;

    &-add{
      display: flex;
      align-items: center;
      justify-content: center;
    }
}

.list-luxury-products {
  padding-bottom: 10px;

  > div {
    display: flex;
    gap: 20px;

    .ant-row {
      display: flex;
      flex-direction: column;

      &:first-child {
        padding-bottom: 10px;
      }
    }
  }
}
